/* Footer.css */

.footer {
    background-color: #333;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
  }
  
  .link {
    color: #fff;
    text-decoration: none;
    font-size: 1.2rem;
    padding: 0.25rem 0.5rem;
  }
  
  .link:hover {
    color: #ddd;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
  }
  